/**
 * Color Palette
 */
:global(.theme-dark) :global .link {
  color: #60AAFF; }

:global(.theme-light) :global .link {
  color: #0065D9; }

:global(.theme-dark) :global .link:hover:not(.disabled) {
  color: #2F7FDB; }

:global(.theme-light) :global .link:hover:not(.disabled) {
  color: #00418C; }

:global .link:focus:not(.disabled) {
  outline-offset: -1px; }
  :global(.theme-dark) :global .link:focus:not(.disabled) {
    outline: 1px dashed #FFFFFF; }
  :global(.theme-light) :global .link:focus:not(.disabled) {
    outline: 1px dashed #000000; }

:global(.theme-dark) :global .link:active:not(.disabled) {
  color: #2F7FDB; }

:global(.theme-light) :global .link:active:not(.disabled) {
  color: #0065D9; }

:global .link.disabled {
  cursor: not-allowed; }
  :global(.theme-dark) :global .link.disabled {
    color: #A6A6A6; }
  :global(.theme-light) :global .link.disabled {
    color: #A6A6A6; }

:global .rtl {
  direction: rtl; }

:global .shell {
  display: flex;
  flex-direction: column;
  height: 100vh; }
  :global(.theme-dark) :global .shell {
    color: #FFFFFF;
    background-color: #1A1D1E; }
  :global(.theme-light) :global .shell {
    color: #000000;
    background-color: #F2F2F2; }

.nav-and-workspace {
  display: flex;
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: relative; }

.workspace {
  flex-grow: 1;
  overflow: auto; }
